import React from 'react';

import styles from './styles.scss';

export interface BoxLayoutProps {
  /** Контент для вставки */
  children: React.ReactNode;

  /** Цвет фона */
  color?: string;
}

export const Box: React.FC<BoxLayoutProps> = ({ children, color = '#fff' }) => (
  <div className={ styles.box } data-test-id="layout_box" style={ { backgroundColor: color } }>
    { children }
  </div>
);
